export default {
	data() {
		return {
			uname:'',
			upwd:'',
			disabled:false,
			openid:''
		};
	},
	methods:{
		login(){
			uni.hideKeyboard()
			if(this.uname == ''){
				uni.showToast({
					title: '请输入手机号码',
					icon: 'none', //loading   none  success
					mask: false,
					duration: 1500
				});
				return false;
			}
			if(!(/^1([38][0-9]|4[579]|5[0-3,5-9]|6[6]|7[0135678]|9[89])\d{8}$/.test(this.uname))){
				uni.showToast({
					title: '手机号码格式不正确',
					icon: 'none', //loading   none  success
					mask: false,
					duration: 1500
				});
				return false;
			}
			if(this.upwd == ''){
				uni.showToast({
					title: '请输入密码',
					icon: 'none', //loading   none  success
					mask: false,
					duration: 1500
				});
				return false;
			}
			this.disabled = true
			this.$store.dispatch('login',{
				uname: this.uname,
				upwd: this.upwd
			}).then(res=>{
				this.disabled = false
				uni.setStorageSync('token',res.data.token)
				uni.switchTab({
					url:'../index/index'
				})
			}).catch(err=>{
				this.disabled = false
			})
		},
		register(){
			uni.navigateTo({
				url:'register'
			})
		},
		forget(){
			uni.navigateTo({
				url:'forget'
			})
		},
